
#app {
    font-size: 18px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
input, select, button {
    text-align: center;
    border-radius: 5px;
    border: 1px solid teal;
    margin: 5px 0;
    height: 25px;
}
#dailycaloriesform, .recipe {
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
