    margin: 5px;
}
h2 {
    text-align: center;
    padding-bottom: 0;
}
h3 {
    text-align: left;
    font-size: 25px;
    margin-top: 0;
}
.dialog {
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0,0,0,0.5);
    position: fixed;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.content {
    margin: auto;
}
.close {
    position: relative;
    top: -25px;
    right: -15px;
    text-align: right;
    color: black;
    font-size: 35px;
    font-weight: bold;
}
.close:hover, .close:focus {
    color: red;
    cursor: pointer;
}
button:hover, button:focus, .currentFilter {
    background-color:steelblue;
    cursor: pointer;
}
.filter {
    margin: auto;
    margin-bottom: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.recipeMeal {
    padding: 0;
    margin: 0;
}
@media (min-width: 1024px)  {
#app {
        width: 100%;
      max-width: 1000px;
  
      margin-left: auto;
      margin-right: auto;
      
      position: relative;
}
}

